body {
  font-family: "Roboto", 'sans-serif';
  color: #333; }

img {
  max-width: 100%;
  height: auto; }

.container {
  padding: 0 15px;
  max-width: 1230px;
  margin: auto;
  overflow: hidden; }

.page-section {
  padding: 1.2rem 0; }
  @media (min-width: 800px) {
    .page-section {
      padding: 4.5rem 0; } }

.text-center {
  text-align: center; }

.b-margin {
  margin-bottom: 1rem; }
  @media (min-width: 530px) {
    .b-margin {
      margin-bottom: 3.875rem; } }

a {
  color: #d59541; }

.large-hero {
  position: relative;
  border-bottom: 10px solid #2f5572; }
  .large-hero__image {
    display: block; }
  .large-hero__text-content {
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    transform: translate(-50%, -50%); }
  .large-hero__title {
    font-size: 2.4rem;
    font-weight: 300;
    color: #2f5572;
    margin: 0; }
    @media (min-width: 530px) {
      .large-hero__title {
        font-size: 4.8rem; } }
  .large-hero__subtitle {
    font-size: 1.5rem;
    font-weight: 300;
    color: #2f5572;
    margin: 0; }
    @media (min-width: 530px) {
      .large-hero__subtitle {
        font-size: 2.5rem; } }
  .large-hero__description {
    color: #fff;
    font-size: 1.1rem;
    font-weight: 100;
    max-width: 30rem;
    text-shadow: 2px 2px 0 rgba(0, 0, 0, 0.1);
    margin: 1rem auto; }
    @media (min-width: 530px) {
      .large-hero__description {
        font-size: 2rem; } }

.btn {
  padding: 0.75rem 1.2rem;
  background-color: #2f5572;
  color: #fff;
  text-decoration: none;
  display: inline-block; }
  .btn__orange {
    background-color: #d59541; }
  .btn__large {
    padding: 1.1rem 1.9rem; }
    @media (min-width: 530px) {
      .btn__large {
        font-size: 1.2rem; } }

.headline {
  font-weight: 300;
  font-size: 1.9rem;
  color: #2f5572; }
  @media (min-width: 530px) {
    .headline {
      font-size: 2.875rem; } }
  .headline--orange {
    color: #d59541; }
  .headline--light {
    font-weight: 100; }
  .headline--small {
    font-size: 1.2rem; }
    @media (min-width: 530px) {
      .headline--small {
        font-size: 1.875rem; } }
  .headline--no-t-margin {
    margin-top: 0; }
  .headline strong {
    font-weight: 400; }
  .headline--narrow {
    max-width: 31.25rem;
    margin: 0 auto; }
  .headline--margin-b-small {
    margin-bottom: 0.5em; }
  .headline--margin-b-large {
    margin-bottom: 1.6em; }

.row::after {
  content: "";
  clear: both;
  display: table; }

.row--gutters {
  margin-right: -4rem; }

.row--gutters > div {
  padding-right: 4rem; }

.row__b-margin-until-medium {
  margin-bottom: 1rem; }

@media (min-width: 800px) {
  .row__b-margin-until-medium {
    margin-bottom: 0; }
  .row__medium-4 {
    float: left;
    width: 33.33%; }
  .row__medium-4-larger {
    width: 37%; }
  .row__medium-8 {
    float: left;
    width: 66.66%; }
  .row__medium-8-smaller {
    width: 63%; } }

.generic-content p {
  line-height: 1.65;
  margin: 1.8rem 0; }
  @media (min-width: 530px) {
    .generic-content p {
      font-size: 1.1875rem; } }
  .generic-content p a {
    font-weight: 700; }
