.row {
  @include clear-fix;

  &--gutters {
    margin-right: -4rem;
  }

  &--gutters > div {
    padding-right: 4rem;
  }

  &__b-margin-until-medium {
    margin-bottom: 1rem;
  }

  @include atMedium {

    &__b-margin-until-medium {
      margin-bottom: 0;
    }

    &__medium-4 {
      float: left;
      width: 33.33%;
    }

    &__medium-4-larger {
      width: 37%;
    }

    &__medium-8 {
      float: left;
      width: 66.66%;
    }

    &__medium-8-smaller {
      width: 63%;
    }
  }
}